/* eslint-disable react/jsx-no-useless-fragment */
// Components
import { QueryRelationship } from "@rp/common/api-types"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Typography, useTheme } from "@rp/react/web/mui/core"
import { Add, Check } from "@rp/react/web/mui/icons"
import { CheckboxStyled } from "./FilterInput.styles"

type placementProps =
  | "bottom-end"
  | "bottom-start"
  | "bottom"
  | "left-end"
  | "left-start"
  | "left"
  | "right-end"
  | "right-start"
  | "right"
  | "top-end"
  | "top-start"
  | "top"

type QueryCheckboxProps = {
  name: string
  queryRelationship: QueryRelationship
  onChange: () => void
  placement?: placementProps
  disableCheckbox?: boolean
}

export function QueryCheckbox({
  name,
  queryRelationship,
  onChange,
  placement,
  disableCheckbox,
}: QueryCheckboxProps) {
  const theme = useTheme()
  const checked = ["direct", "indirect"].includes(queryRelationship ?? "result")
  const inWatchlist = ["indirect"].includes(queryRelationship ?? "result")

  const title = () => {
    if (inWatchlist) {
      return (
        <>
          <Typography variant="subtitle2" fontWeight="bold" sx={{ color: theme.palette.grey[100] }}>
            {name} is in your Watchlists
          </Typography>
          <Typography variant="subtitle2" sx={{ color: theme.palette.grey[300] }}>
            Watchlist constituents can't be removed from your query
          </Typography>
        </>
      )
    }
    if (checked) {
      return <Typography variant="subtitle2">Remove from search</Typography>
    }

    return <Typography variant="subtitle2">Add to Search</Typography>
  }

  return (
    <RPTooltip enterDelay={500} title={title()} placement={placement}>
      <CheckboxStyled
        checked={checked}
        onChange={onChange}
        color="default"
        size="small"
        icon={<Add />}
        checkedIcon={<Check />}
        data-testid={name}
        inWatchlist={inWatchlist}
        disabled={disableCheckbox}
        disableCheckbox={Boolean(disableCheckbox)}
      />
    </RPTooltip>
  )
}
